@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
:root {
    --text-color: #969696;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Raleway', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    display: flex;
    max-width: 1440px;
    max-height: 729px;
}

.info-container {
    padding: 3rem;
    width: 100%;
}

.search-container {
    text-align: center;
}

.image {
    height: 100vh;
    margin-left: auto;
    width: 750px;
    max-height: 729px;
    background-size: cover;
}

.btn-search {
    border-radius: 50%;
    height: 32px;
    width: 32px;
    border: none;
    cursor: pointer;
    position: relative;
    right: 35px;
    background-color: hotpink;
    color: white;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
    transition: 0.3s ease-in-out;
    outline: none;
}

.search {
    border: 1px solid var(--text-color);
    padding: 10px;
    border-radius: 20px;
    outline: none;
    width: 200px;
    transition: 0.3s ease-in-out;
}

.search:focus {
    width: 400px;
}

.search:focus + .btn-search {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

.no-image {
    height: 0;
    width: 0;
}
.Squall-image,
.Thunderstorm-image {
    background-image: url(/weather/static/media/thunderstorm.ee324534.jpeg);
}
.Sand-image,
.Clear-image {
    background-image: url(/weather/static/media/clear.48a2b5b8.jpeg);
}
.Mist-image,
.Rain-image,
.Drizzle-image {
    background-image: url(/weather/static/media/rain.6e18a2f4.jpeg);
}

.Snow-image {
    background-image: url(/weather/static/media/rain.6e18a2f4.jpeg);
}
.Haze-image,
.Dust-image,
.Fog-image,
.Clouds-image {
    background-image: url(/weather/static/media/cloudy.d5c3e454.jpeg);
}
.Ash-image,
.Smoke-image,
.Tornado-image {
    background-image: url(/weather/static/media/atmosphere.e94e3281.jpeg);
}

@media (max-width: 1100px) {
    .image {
        display: none;
    }
}

@media (min-width: 1440px) {
    .App-container {
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .App {
        background-color: #fff;
        width: 1440px;
        height: 729px;
        box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    }
    body {
        background: rgb(34, 193, 195);
        background: linear-gradient(94deg, rgba(34, 193, 195, 1) 0%, rgba(253, 187, 45, 1) 100%);
    }
}

.Dashboard {
    color: var(--text-color);
    text-transform: uppercase;
    margin-top: 50px;
}

.Dashboard-date {
    margin: 0;
    letter-spacing: 1.5px;
    font-size: 3rem;
}

.Dashboard-temperature-container {
    display: flex;
}

.Dashboard-temperature {
    font-size: 11rem;
    margin: 0;
}

.Dashboard-weekly-container {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
}

.Dashboard-day-container {
    font-size: 1.3rem;
    display: flex;
    flex-direction: column;
    text-align: center;
}

@media (max-width: 800px) {
    .Dashboard-weekly-container {
        flex-wrap: wrap;
    }
}

.Thunderstorm {
    color: #7f5ead;
}

.Clear {
    color: #d0a870;
}

.Rain,
.Drizzle {
    color: #74c1df;
}

.Snow {
    color: #b39089;
}

.Clouds {
    color: #a8c0b5;
}

.Welcome {
    margin-top: 100px;
}

.Welcome-message {
    text-align: center;
}

.Error {
    margin-top: 100px;
}

.Error h4 {
    text-align: center;
    font-size: 20px;
}

.Error-message {
    color: red;
}

/* Demo Styles */

/* The Loader */
#loader-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    overflow: hidden;
}
.no-js #loader-wrapper {
    display: none;
}

#loader {
    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #16a085;
    -webkit-animation: spin 1.7s linear infinite;
    animation: spin 1.7s linear infinite;
    z-index: 11;
}
#loader:before {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #e74c3c;
    -webkit-animation: spin-reverse 0.6s linear infinite;
    animation: spin-reverse 0.6s linear infinite;
}
#loader:after {
    content: '';
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #f9c922;
    -webkit-animation: spin 1s linear infinite;
    animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}
@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@-webkit-keyframes spin-reverse {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(-360deg);
    }
}
@keyframes spin-reverse {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(-360deg);
    }
}
#loader-wrapper .loader-section {
    position: fixed;
    top: 0;
    width: 51%;
    height: 100%;
    background: #fff;
    z-index: 10;
}

#loader-wrapper .loader-section.section-left {
    left: 0;
}

#loader-wrapper .loader-section.section-right {
    right: 0;
}

/* Loaded styles */
.loaded #loader-wrapper .loader-section.section-left {
    transform: translateX(-100%);
    transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.loaded #loader-wrapper .loader-section.section-right {
    transform: translateX(100%);
    transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.loaded #loader {
    opacity: 0;
    transition: all 0.3s ease-out;
}

.loaded #loader-wrapper {
    visibility: hidden;
    transform: translateY(-100%);
    transition: all 0.3s 1s ease-out;
}

